import { Directive, OnInit, Input } from '@angular/core';
import { BaseActionDispatcher } from '../action/BaseActionDispatcher';

@Directive({
  selector: '[ao-actions]'
})
export class AoActionsDirective implements OnInit {

  @Input() actionDispatcher;
  
  constructor(private baseActionDispatcher: BaseActionDispatcher) { }

  ngOnInit(): void {
    this.baseActionDispatcher.setActionDispatcher = this.actionDispatcher;
  }
}
